input {
  width: 100%;
  max-width: 336px;
  height: 2px;
  margin: 0 29.94px 0 23.54px;
}

input[type="range"] {
  -webkit-appearance: none;
  outline: none;
  background: transparent;
}

input[type="range"]::-webkit-slider-runnable-track {
  -webkit-appearance: none;
  height: 2px;
  cursor: pointer;
  background: rgba(255, 255, 255, 0.2);
}

input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 26px;
  width: 26px;
  border-radius: 50%;
  background: #afb3bf;
  box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  margin-top: -12px;
  outline: none;
}

input[type="range"]::-moz-range-track {
  height: 2px;
  cursor: pointer;
  background: rgba(255, 255, 255, 0.2);
}

input[type="range"]::-moz-range-thumb {
  height: 26px;
  width: 26px;
  border-radius: 50%;
  background: #afb3bf;
  box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  margin-top: -12px;
  outline: none;
}

input[type="range"]::-moz-range-progress {
  background: rgba(255, 255, 255, 0.5);
  height: 2px;
}

input[type="range"]::-ms-track {
  height: 2px;
  background: rgba(255, 255, 255, 0.5);
  border-color: transparent;
  border-width: 6px 0;
  color: transparent;
}

input[type="range"]::-ms-thumb {
  border: none;
  height: 26px;
  width: 26px;
  border-radius: 50%;
  background: #afb3bf;
  box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  margin-top: -12px;
  outline: none;
}
