.number-input {
  width: 272px;
  height: 368px;
  display: flex;
  justify-content: space-between;
  align-items: space-between;
  flex-wrap: wrap;
  .btn__circle {
    border: 2px solid rgba(255, 255, 255, 0.1);
  }
  img {
    width: 30px;
    height: 30px;
  }
}

.number-input.classrooms__number-input {
  width: 255px;
  height: 345px;
  margin-top: 49px;
  display: none;
  .btn__circle {
    width: 75px;
    min-width: 75px;
    height: 75px;
  }

  .number-input__item-disabled {
    visibility: hidden;
  }

  .mobile-number-input__item {
    display: none;
  }
}

.numbers-panel {
  display: grid;
  grid-gap: 16px;
  grid-template-columns: repeat(3, 80px);
  .btn__circle {
    border: 2px solid rgba(255, 255, 255, 0.1);
  }
}

@media all and (max-width: 850px) {
  .numbers-panel {
    grid-template-columns: repeat(3, 64px);
    .btn__circle {
      min-width: 64px;
      width: 64px;
      height: 64px;
      font-size: 22px;
      line-height: 26px;
      border: 2px solid hsla(0,0%,100%,.3);
    }
  }
  .speed-dial__item,
    .number__output,
    .btn__circle {
      font-size: 22px;
      line-height: 26px;
    }
}

